.products {
    margin: 20px auto;
    width: 80%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    background-color: rgb(82, 77, 77) !important;
    border-radius: 5px;
}
.card {
    width: 300px;
    height: 445px;
    border: 1px solid black;
    border-radius: 5px;
    overflow: hidden;
    padding: 10px;
    margin: 40px 20px;
    background-color: white;
}
.product-image {
    width: 80%;
    max-width: 300px;
    min-height: 278px;
    display: block;
    object-fit: cover;
}
.product-name {
    text-transform: uppercase;
    margin: 10px 0px;
}
.product-price {
    margin: 10px 0px;
    padding-bottom: 20px;
    font-size: 18px;
    line-height: 30px;
}
.product-add-button {
    font-weight: 700;
    font-size: 18px;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: black;
    color: white;
    width: 100%;
    height: 40px;
    display: block;
    cursor: pointer;
    text-transform: uppercase;
    margin: 15px auto;
}
.product-add-button:hover {
    background-color: brown;
    transition: .4s;
}
.product-add-button:active {
    transform: translateY(2px);
}


@media only screen and (max-width:768px){
    .logo {
        text-decoration: none;
        text-transform: uppercase;
        color: white;
        padding-left: 15px;
        font-size: 20px;
    }
}


@media only screen and (max-width:585px){
    .logo {
        text-decoration: none;
        text-transform: uppercase;
        color: white;
        padding-left: 9px;
        font-size: 16px;
    }
}

@media only screen and (max-width:848px) {
    .product-image {
        width: 80%;
        max-width: 60px;
        min-height: 60px;
        display: block;
        object-fit: cover;
    }
}
